form {
    padding:2em 0;
}
input {
    display:block;
    padding:2em 1em 1em;
    width:100%;
}
button {
    display:inline-block;
    margin:0 1em 0 0;
}
#createParticipant {
    background: #f76c6c;
    border: none;
    color: #000;
    font-weight: 700;
    padding: 1em 2em;
}
.registration label {
    font-size:.75em;
    position:absolute;
    top:1em;
    left:1em;
}



.playerArea {
    float:left;
    padding:2em;
    width:33.33%;
}
.player{
    float:left;
    margin:1%;
    position: relative;
    width:48%;
}
.eliminated {
    opacity:25%;
}
.playerAvatar {
    border-radius:50%;
    width:100%;
}
.playerName {
    color: #fff;
    position: absolute;
    top: 1em;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    text-shadow: 1px 1px 2px rgba(0,0,0,.5);
}
.playerScore {
    background: #24305e;
    border: 2px solid #f8e9a1;
    border-radius: 50%;
    color: #fff;
    font-size: 2em;
    height: 2em;
    line-height: 2em;
    width: 2em;
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 0;
}
.responseArea {
    float:left;
    padding:2em;
    width:66.66%;
}
.response {
    font-size:1.25em;
    background: #f8e9a1;
    color: #000;
    margin:1em 0;
    padding:.5em 1em;
}
.dragHover {
    border:2px solid red;
}